.SearchResultList {
  overflow: hidden;
  height: 100%;
}
.Header {
  display: flex;
  align-items: center;
}
.Header img {
  width: 50px;
  margin-right: 20px;
}
.SearchResult {
  transform: translateY(100vh);
}
@keyframes slide {
  to {
    transform: translateY(0);
  }
}
.ResultPlaceholder {
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
}
